<template>
  <div class="buypage-container">
    <h1 class="header"></h1>
    <div class="buy-box">
      <div class="buypage-main">
        <div class="list">
          <h2 class="_hr">/</h2>
          <div class="formBox">
            <div class="formData">
              <label>
                <span class="star">*</span>
                
                <input class="ml-10" v-model="purchase.m_nm" type="text" />
              </label>
            </div>
            <div class="formData">
              <label>
                <span class="star">*</span>
                
                <select class="telInput ml-10" @change="changeTel1" v-model="purchase.m_tel1">
                  <option v-for="(code, idx) in AreaCode" :key="idx" :value="code">{{ code }}</option>
                </select>
              </label>
              - <input v-model="purchase.m_tel2" class="telInput" type="text" /> -
              <input v-model="purchase.m_tel3" class="telInput" type="text" />
            </div>
            <div class="formData">
              <label>
                <span class="star">*</span>
                 <input class="emailInputBox ml-10" v-model="purchase.m_email" type="text"
              /></label>
            </div>
            <!--   -->
            <div class="formData">
              <div>
                <span class="star">*</span>
                 
                <button ref="addrBtn1" class="addrBtn activeBd ml-8" @click="toggleBtn"> </button>
                <button ref="addrBtn2" class="addrBtn" @click="toggleBtn"> </button>

                <div class="addrBox" v-if="userAddr === 0">
                  {{ purchase.m_addr }}
                </div>
                <div class="addrBox" v-else>
                  <label></label>
                  <div>
                    <div class="row">
                      <div class="col-md-3 mb-3">
                        <input type="text" v-model="purchase.postcode" class="form-control" placeholder="" />
                      </div>
                      <div class="col-md-3 mb-3">
                        <input type="button" @click="execDaumPostcode()" class="btn btn-secondary my-1 btn-sm" value=" " />
                      </div>
                    </div>
                    <div>
                      <input type="text" v-model="purchase.m_addr" class="form-control mb-3" placeholder="" />
                      <input type="text" v-model="purchase.detailAddr" class="form-control mb-3" placeholder="" />
                      <input type="text" v-model="purchase.extraAddr" class="form-control mb-3" placeholder="" />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="formData">
              <label class="formData">
                <span class="star">*</span>
                 
                <select class="" v-model="RequireOption">
                  <option v-if="RequireOption === '   .'">{{ RequireOption }}</option>
                  <option v-for="(option, idx) in Requirements" :key="idx" :value="option">{{ option }}</option>
                </select>
                <div>
                  <input
                    class="longInputBox"
                    v-if="RequireOption === '  '"
                    type="text"
                    placeholder="  .( 45 )"
                  />
                </div>
              </label>
            </div>
          </div>
          <hr />
        </div>
        <!-- ---------------------------- -->
        <div class="list">
          <h2 class="_hr">  </h2>
          <div class="orderInfo">
            <!-- {{ productDetail.pro_mainimg }} -->
            <img :src="this.$getSrc(productDetail.pro_mainimg)" />
            {{ productDetail.pro_name }}
            <div><input type="number" min="1" v-model="purchase.pro_count" @change="getPrice" /></div>
            <div>{{ $addComma(price()) }}</div>
          </div>
          <hr />
        </div>
        <!-- ---------------------------- -->
        <div class="list">
          <h2 class="_hr"> </h2>
          <div class="mt">
            <span v-for="(payment, idx) in payment" :key="idx" class="changePayment">
              <input @change="changePayment" :value="payment" type="radio" name="payment" v-model="paymentCate" /> {{ payment }}
            </span>
          </div>
          <hr />
          <div class="mb-5 column-center" v-show="selectPayment === 0">
            <img style="width: 800px; cursor: pointer" src="@/assets/img/bank.png" />
            <span class="interest"></span>
          </div>
          <div class="mb" v-show="selectPayment === 1">
            <button class="cardBtn"><span class="cardBtnImg"> </span></button>
            <div class="buyCaution">
                    /     .
            </div>
          </div>
          <hr />
        </div>
        <!-- ---------------------------- -->

        <div class="mb-6">
          <h3></h3>
                         . <br />
           ID                .
          <br />
            : , , , BC, , , , NH, , 
          <br />
            : NH, , , , , SC, , , , , , , , , , ,
          , , , , 
          <br />
                 ,     .
          <br />
                .
          <br />
           / :    3~5  (/  ) <br />
            :    (,    )
          <br />
           :    
          <div>      /     .</div>
        </div>
      </div>
      <!-- -------------------------------------------------------- -->
      <section class="buy-section">
        <h3> </h3>
        <div class="sectionBuyBox">
          <div class="flex-between buy-section-txt">
            <div></div>
            <div>{{ $addComma(price()) }}</div>
          </div>
          <!-- ---------------------------- -->
          <div class="flex-between buy-section-txt">
            <div></div>
            <div class="d-flex">
              <div class="discount">0</div>
              
            </div>
          </div>
          <!-- ---------------------------- -->
          <div class="flex-between buy-section-txt">
            <div></div>
            <div></div>
          </div>
          <!-- ---------------------------- -->
          <hr />
          <div class="flex-between total-amount">
            <div class="txt"></div>
            <div class="num">{{ $addComma(price()) }}</div>
          </div>
          <!-- ---------------------------- -->
          <div class="essential">
            <label> <input type="checkbox" v-model="payAgree" /> []     </label>
            <div>
                , ,  ,    ? <br />
              ( 8 2)
            </div>
          </div>
        </div>
        <div class="buyBtn">
          <button @click="buy">{{ $addComma(price()) }} </button>
        </div>
      </section>
      <!-- -------------------------------------------------------- -->
    </div>
  </div>
</template>
<script>
import router from '@/router';

export default {
  props: {
    name: {
      type: String,
      default: '',
    },
  },
  components: {},
  data() {
    return {
      AreaCode: ['010', '011', '016', '017', '018', '019'],
      Requirements: [
        ' () .',
        '   ',
        '    ,  .',
        '  .',
        '  ',
      ],
      payment: [' ', ' '],
      paymentCate: ' ',
      RequireOption: '   .',
      productDetail: '',
      selectPayment: 0,
      payAgree: false,
      purchase: {
        pro_num: 0,
        m_num: 0,
        m_nm: '',
        m_email: '',
        m_tel1: '010',
        m_tel2: '',
        m_tel3: '',
        postcode: '',
        m_addr: '',
        detailAddr: '',
        extraAddr: '',
        pro_count: 1,
        pro_price: 0,
      },
      userPhNum: [],
      userAddr: 0,
    };
  },

  computed: {
    loginToggle: function () {
      return this.$store.state.setUser;
    },
  },
  watch: {
    loginToggle: function () {
      this.loginCheck();
    },
  },
  beforeCreate() {},
  created() {
    this.productDetail = this.$store.state.getProductList[this.$route.params.productId - 1];
    this.purchase.pro_price = this.productDetail.pro_price;
    this.purchase.pro_num = this.productDetail.pro_num;
    this.purchase.m_num = this.$store.state.user.result.m_num;
    this.purchase.m_nm = this.$store.state.user.result.m_nm;
    this.purchase.m_email = this.$store.state.user.result.m_email;
    this.purchase.m_addr = this.$store.state.user.result.m_addr;
    this.getUserTel();
    console.log(this.$store.state.user.result);
    console.log(this.purchase);
  },
  beforeMount() {},
  beforeUpdate() {},
  updated() {},
  unmounted() {},
  mounted() {
    window.addEventListener('beforeunload', this.unLoadEvent);
  },
  beforeUnmount() {
    window.removeEventListener('beforeunload', this.unLoadEvent);
  },
  methods: {
    unLoadEvent: function (event) {
      if (this.canLeaveSite) return;

      event.preventDefault();
      event.returnValue = '';
    },
    getUserTel() {
      const userTel = this.$store.state.user.result.m_tel.split('-');
      this.purchase.m_tel2 = userTel[1];
      this.purchase.m_tel3 = userTel[2];
    },
    changeTel1(e) {
      this.purchase.m_tel1 = e.target.value;
    },
    loginCheck() {
      if (!this.$store.state.user) {
        alert('    .');
        this.$router.push('signin');
      }
    },
    logout() {
      this.$store.commit('user', null);
      this.$router.push('signin');
    },
    price() {
      return this.purchase.pro_count * this.productDetail.pro_price;
    },
    changePayment() {
      if (this.paymentCate === this.payment[0]) {
        this.selectPayment = 0;
      } else if (this.paymentCate === this.payment[1]) {
        this.selectPayment = 1;
      } else {
        this.selectPayment = 2;
      }
    },
    async buy() {
      if (this.payAgree === false) {
        alert('    .');
        return;
      }
      console.log(this.purchase);
      const productBuy = await this.$post('product/productBuy', this.purchase)
        .then((response) => {
          console.log(response);
          alert(' .');
          router.push('/myPageMemberCheck');
        })
        .catch((error) => {
          console.log(error.response);
          alert(' .');
        });
    },
    toggleBtn(e) {
      this.userAddr -= 1;
      this.userAddr *= -1;
      this.$refs.addrBtn1.classList.remove('activeBd');
      this.$refs.addrBtn2.classList.remove('activeBd');
      e.target.classList.add('activeBd');
      console.log(this.userAddr);
    },
    execDaumPostcode() {
      new window.daum.Postcode({
        oncomplete: (data) => {
          if (this.purchase.extraAddr !== '') {
            this.purchase.extraAddr = '';
          }
          if (data.userSelectedType === 'R') {
            //     
            this.purchase.m_addr = data.roadAddress;
          } else {
            //     (J)
            this.purchase.m_addr = data.jibunAddress;
          }

          //       .
          if (data.userSelectedType === 'R') {
            //     "//" .
            if (data.bname !== '' && /[||]$/g.test(data.bname)) {
              this.purchase.extraAddr += data.bname;
            }
            //  ,   .
            if (data.buildingName !== '' && data.apartment === 'Y') {
              this.purchase.extraAddr += this.purchase.extraAddr !== '' ? `, ${data.buildingName}` : data.buildingName;
            }
            //    ,     .
            if (this.purchase.extraAddr !== '') {
              this.purchase.extraAddr = `(${this.purchase.extraAddr})`;
            }
          } else {
            this.purchase.extraAddr = '';
          }
          //  .
          this.purchase.postcode = data.zonecode;
        },
      }).open();
    },
    getPrice() {
      this.purchase.pro_price = this.purchase.pro_count * this.productDetail.pro_price;
    },
  },
};
</script>

