              </div>
            </div>
            <div class="review">
              <div class="r_info_title">   </div>
              <div>
                <div class="info_sub_title">[  ]</div>
                <ul>
                  <li class="info_ctnt">
                      ,   ,    ,        
                       .
                  </li>
                  <li class="info_ctnt">
                        /,   ,            
                         .
                  </li>
                  <li class="info_ctnt">
                              1   ,   1 .
                  </li>
                  <li class="info_ctnt">    1        .</li>
                </ul>
              </div>
              <div>
                <div class="info_sub_title">[  ]</div>
                <ul>
                  <li class="info_ctnt">
                    2022 1 1      ()   2,000,000P   
                         50  ,   .
                  </li>
