                    <div class="pb-2"><input type="checkbox" />   </div>
                    <div class="Precautions pb-3">   .</div>
                  </div>
                  <div class="cursor"> ></div>
                </div>
                <div>
                  <input type="button" value="" class="btn btn-secondary cansle" />
                  <input type="submit" value="" class="btn btn-dark" />
                </div>
              </form>
